import axios from 'axios'
import { services } from '../../core/backbone'
import FronthubError from '../../core/helpers/FronthubError'

const servicesList = services()

export const client = {
  create(name, configuration = {}) {
    const service = servicesList.find(item => {
      return item && item.name === name
    })
    if (!service) {
      throw new FronthubError(`service ${name} was not found in your FrontHub configuration\n
      Services found: ${servicesList.join('\n')}`)
    }
    return axios.create({
      ...configuration,
      baseURL: `${service.endpoint}`,
    })
  },
}
