import { useContext, useState, useEffect } from 'react'

import { FronthubContext } from 'front-hub-context-api'

import { isPromise } from '../../../../core/object'
import { parseToSafeJson } from '../../../../core/security'

export default function useHostContext() {
  const { context } = useContext(FronthubContext)
  const [contextValue, setContextValue] = useState()

  useEffect(() => {
    if (isPromise(context)) {
      context.then(setContextValue)
    }
  }, [context])

  return contextValue ? { ...parseToSafeJson(contextValue) } : {}
}
