import { Theme, Reset } from '@resultadosdigitais/tangram-components'
import { Connect } from '@resultadosdigitais/front-hub/react'

import Welcome from './Welcome'

export default Connect(
  () => (
    <Theme value={Theme.kinds.lina}>
      <Reset />
      <Welcome />
    </Theme>
  ),
  {
    intlFormatting: function (config, context) {
      return function (value, format, lng) {
        if (format === 'uppercase')
          return `${value.toUpperCase()} - ${context.user.name}`
        return value
      }
    },
  },
)
