/* eslint-disable import/no-unresolved */
import i18next from 'i18next'
import resources from 'front-hub-locales'
import FronthubError from '../../../core/helpers/FronthubError'
/**
 * This function to create the i18next instance configured according to parameters
 * @param {object}
 * - `configuration` object with at least the intl property `{intl: {}}`
 * - `intlFormatting` function which is responsible to interpolate the intl formatting
 * - `plugins` array containing all plugins to be used in i18n instance
 * @returns The i18next instance
 */
const i18nFactory = ({ configuration, intlFormatting, plugins = [] }) => {
  const name = configuration.name || MICROFRONTEND_NAME
  const { intl, context } = configuration
  const i18n = i18next.createInstance()

  if (typeof intlFormatting !== 'function') {
    throw new FronthubError('the addon `intlFormatting` should be a function')
  }

  const handleFormatting = intlFormatting(intl, context)

  if (typeof handleFormatting !== 'function') {
    throw new FronthubError(
      'the addon `intlFormatting` should return a function',
    )
  }

  plugins.forEach(i18n.use.bind(i18n))

  i18n.init({
    lng: intl.language,
    fallbackLng: intl.fallbackLng,
    debug: intl.debug,
    interpolation: {
      ...intl.interpolation,
      format: handleFormatting,
    },
    resources: Object.keys(resources).reduce(
      (acc, lgn) => ({
        ...acc,
        [lgn]: {
          [name]: resources[lgn],
        },
      }),
      {},
    ),
    ns: [name],
    defaultNS: name,
  })
  return i18n
}

export default i18nFactory
