import React, { useContext, useState } from 'react'

import { FronthubContext } from 'front-hub-context-api'

import { isPromise } from '../../../core/object'
import { parseToSafeJson } from '../../../core/security'

export default function withHostContext(Component) {
  return function LazyContext(props) {
    const { context } = useContext(FronthubContext)
    const [contextValue, setContextValue] = useState(context)

    if (isPromise(contextValue)) {
      contextValue.then(setContextValue)
    }

    return isPromise(contextValue) ? null : (
      <Component {...parseToSafeJson(contextValue)} {...props} />
    )
  }
}

/**
 * @deprecated renamed to withHostContext
 */
export function withContext(Component) {
  console.warn(
    'FrontHub `withContext` is deprecated, please use `withHostContext` instead',
  )
  return withHostContext(Component)
}
